import { validateForm } from '@/helpers/validation'

export default {
	name: 'OfficeForm',
	props: {
		office: {
			type: Object,
			required: false,
			default: () => {}
		},
		open: {
			type: Boolean,
			required: false,
			default: false
		}
	},
	data: () => {
		return {
			form: {
				id: null,
				title: '',
				address: '',
				contact: {
					name: '',
					position: '',
					email: '',
					phone: ''
				}
			},
			isSubmitted: false,
			isNew: true
		}
	},
	computed: {
		isEditing() {
			return this.office && this.office.id
		}
	},
	created() {
		if (this.isEditing) {
			this.isNew = false
			this.form = {
				...this.office,
				contact: {
					...this.office.contact
				}
			}
		}
	},
	mounted() {
		if (!this.isEditing) {
			this.$refs.title.$el.focus()
			this.isNew = false
		}
	},
	methods: {
		onSubmit() {
			this.isSubmitted = true
			if (!this.isValid()) return false

			this.$store.dispatch('updateStore', {
				...this.form
			})

			this.$emit('close')
		},
		isValid() {
			return validateForm(this.$refs)
		}
	}
}
